<template>
  <div>
    <form>
      <v-card flat>
        <v-card-title class="ml-4" style="font-size:2rem">
          <span></span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field
                  v-model="name"
                  label="()"
                  type="text"
                  v-validate="'required|min:2'"
                  :error-messages="errors.collect('name')"
                  name="name"
                  required
                ></v-text-field>
              </v-flex>

              <v-flex xs12>
                <v-text-field
                  v-model="email"
                  label="Email()"
                  type="email"
                  v-validate="'required|email'"
                  :error-messages="errors.collect('email')"
                  name="email"
                  required
                ></v-text-field>
              </v-flex>
              <v-flex xs12>
                <v-text-field
                  v-model="password"
                  label=""
                  type="password"
                  v-validate="'required|min:6'"
                  :error-messages="errors.collect('password')"
                  name="password"
                  required
                ></v-text-field>
              </v-flex>
              <v-layout row justify-center xs8 sm4 md2 mt-3>
                <v-btn round color="primary" @click="submit();"></v-btn>
              </v-layout>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
        </v-card-actions>
      </v-card>
    </form>
  </div>
</template>

<script>
import { apiUserRegister } from "../../utils/api.js";

export default {
  name: "RegisterForm",
  data: () => ({
    name: "",
    email: "",
    password: "",
    success: null,
    errMsg: ""
  }),

  methods: {
    submit() {
      this.$validator.validateAll();
      this.$bus.$emit('isLoading:push',true)
      
      apiUserRegister({
        name: this.$data.name,
        account: this.$data.email,
        password: this.$data.password,
        userToken:'',
        type: 0,
      })
        .then(res => {
          //
          if (res.data.status === 400) {
            this.$data.errMsg = res.data.msg;
            this.$data.success = false;
            //
            this.$bus.$emit('message:push',this.$data.errMsg)
            this.$bus.$emit('isLoading:push',false)

            return;
          }
          //token
          let expireDays = 1000 * 60 * 60 * 24 * 1;
          this.setCookie("session", res.data.token, expireDays);
          this.$data.success = true;
          //
          this.$store.commit("updateLoginState", this.$data.success);
          this.$store.commit("updateUserInfo", res.data);
          this.$store.commit("updateDrawer", this.$data.success);
          //
          this.$bus.$emit('isLoading:push',false)
          this.$emit("closeDialog");
          this.$router.go("/");
          this.$bus.$emit('message:push','')

        })
        .catch(err => {
          //
          console.log(err);
          this.$data.success = false;
          this.$bus.$emit('isLoading:push',false)
          this.$bus.$emit('message:push','','error')
        });
    },
    clear() {
      this.email = "";
      this.password = "";
      this.name = "";
      this.$validator.reset();
    }
  }
};
</script>
